import { useEffect, useState } from "react"
import Hero from "../component/Hero"
import Carrusel from "../component/Carrusel"
import Card from "../component/Card"
import ItemCard from "../component/ItemCard"
import getData from "../utils/getData"
const Home = ()=>{
    const [anuncios, setAnuncios] = useState([])
    useEffect(()=>{
        getData().then((doc)=>{
            doc.ads.map((item)=>{
                console.log(item)
                setAnuncios(prevArray=>[...prevArray, item])
            })
        })
    }, [])
    return(
        <div>
           <Hero titular="Hola como estas"/>
           <h1>Bienvenidos al App de Ciudad Quetzal</h1>
           <Carrusel>
           <Card />
           <Card />
           <Card />
           <Card />
           </Carrusel>
          <h2>Ademas de Noticias puedes realizar tus compras desde nuestra App</h2>
          <Carrusel>
           {
               anuncios.map((item)=>{
                return <ItemCard  titulo={item.ad.titulo} imagen={item.ad.imagen} descripcion={item.ad.descripcion} key={item.adid} id={item.adid}/>
               })
           }
           </Carrusel>
        </div>
    )
}
export default Home