import { useEffect, useState } from "react"
import getData from "../utils/getData"
import "../styles/Anuncio.css"
const Anuncio = ()=>{
    
    const [detalles, setDetalles] = useState([])
    const [loading, setLoading] = useState(true)
    const url = window.location.pathname.toString().slice(1)
    console.log(url)
    useEffect(()=>{
        getData(url).then((doc)=>{
           setDetalles(doc)
           setLoading(false)
        })
    }, [])
    if (loading){
        
       return <img className="loading" src="./img/loading.gif" alt="" />
    }else{
        return(
            <div className="container">
                <div>
                <img className="detalles-img" src={detalles.datos.imagen} alt="" />
                <div className="detalles">
                    <span>{detalles.datos.categoria}</span>
                    <span>Precio: <b>Q {detalles.datos.precio}</b></span>
                </div>
                </div>
                <div className="detalles-text">
                <h2>{detalles.datos.titulo}</h2>
                <p className="texto">{detalles.datos.descripcion}</p>
                </div>
             
            </div>
        )
    }
  
    
}
export default Anuncio